.purchase-main {
  padding: 4.0625em 0 3.125em 0;
  &__title {
    text-align: center;
  }
  &__content {
    background: #F6F6F6;
    border-radius: 1.875em;
    padding: 3.125em 5.625em;
    margin-top: 1.875em;
    position: relative;
    overflow: hidden;
  }
  &__img {
    position: absolute;
    top: 50%;
    right: -1.25em;
    transform: translate(0, -50%);
    width: 74.6875em;
    height: 100%;
    display: block;
    pointer-events: none;
  }
  &__flex {
    display: flex;
    align-items: center;
    position: relative;
    z-index: 2;
  }
  &__list {
    width: 37.5em;
    margin-right: 6.25em;
    li {
      display: flex;
      &:not(:first-child) {
        margin-top: 2em;
      }
      i {
        flex-shrink: 0;
        width: 2.375em;
        margin-right: 0.9375em;
        img {
          width: 100%;
        }
      }
    }
  }
  &__btn {
    width: 22.25em;
    height: 4.125em;
    border-radius: 3.125em;
  }
}
.purchase-info {
  padding: 3.125em 0;
  &__inner {
    text-align: center;
  }
  &__title {
    text-align: center;
  }
  &__desc {
    margin-top: 1.375em;
    p {
      span {
        color: #2C4158;
      }
      &:not(:first-child) {
        margin-top: 1.375em;
      }
    }
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 3.125em / 2));
    gap: 3.125em;
    margin-top: 2.5em;
  }
  &__item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 14.25em;
    border-radius: 1.875em;
    position: relative;
    &-img {
      img {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        border-radius: 1.875em;
      }
    }
    &-title {
      font-weight: 600;
      color: #2C4158;
    }
    &-text {
      margin-top: 0.75em;
      color: #383838;
    }
  }
  .border {
    border: 0.0625em solid #C98B94;
    padding: 1.5625em 2.1875em;
  }
}
.purchase-info2 {
  padding: 3.125em 0;
  &__inner {
    text-align: center;
  }
  &__subtitle {
    margin-top: 1.875em;
  }
  &__items {
    width: calc(100% + 1.1875em);
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
    margin-top: 3.125em;
  }
  &__item {
    min-height: 8.75em;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex: 1 0 calc(25% - 1.1875em);
    margin: 0 1.1875em 1.1875em 0;
    border: 0.0625em solid #B88571;
    border-radius: 1.875em;
    padding: 1em;
    &-title {
      span {
        font-weight: 600;
        color: #2C4158;
      }
    }
    &-subtitle {
      span {
        color: #2C4158;
        font-weight: 600;
      }
    }
    &-text {
      margin-top: 0.625em;
    }
  }
}
.purchase-evaluation {
  padding: 3.125em 0;
  &__inner {
    text-align: center;
  }
  &__subtitle {
    margin-top: 1.875em;
    span {
      font-weight: 600;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 18.75em / 4));
    gap: 6.25em;
    margin-top: 3.125em;
  }
  &__item {
    background: #F6F6F6;
    padding: 0.9375em 1.25em;
    position: relative;
    &-icon {
      position: absolute;
      top: 50%;
      right: -4.0625em;
      transform: translate(0, -50%);
      width: 1.875em;
    }
    &-number {
      span {
        font-size: 3.75em;
        line-height: 120%;
        font-weight: 600;
        color: #2C4158;
      }
    }
    &-title {
      color: #B88571;
      margin-top: 0.3125em;
      span {
        font-weight: 600;
      }
    }
    &-text {
      margin-top: 0.3125em;
    }
  }
}
.purchase-connection {
  padding: 3.125em 0;
  &__inner {
    background: #F6F6F6;
    border-radius: 1.875em;
    padding: 2.625em 4.375em 4.375em;
    position: relative;
    overflow: hidden;
  }
  &__img {
    position: absolute;
    top: 50%;
    right: -1.25em;
    transform: translate(0, -50%);
    width: 74.6875em;
    height: 100%;
    display: block;
    pointer-events: none;
  }
  &__form {
    display: flex;
    align-items: center;
    margin-top: 2em;
    position: relative;
    z-index: 2;
    &-group {
      padding: 0;
      margin: 0;
      border: navajowhite;
      &:not(:first-child) {
        margin-left: 4.8125em;
      }
      input {
        width: 21.625em;
        height: 2.1875em;
        border: none;
        background: transparent;
        border-bottom: 0.0625em solid #383838;
        padding: 0 0.9375em;
        font-size: 1em;
        line-height: 120%;
        font-weight: 300;
        color: #383838;
      }
    }
    &-btn {
      width: 22.8125em;
      height: 4.125em;
      border-radius: 3.125em;
      margin-left: 7em;
    }
  }
}
.purchase-desc {
  padding: 3.125em 0;
  &__inner {
    text-align: center;
  }
  &__text {
    margin-top: 2.5em;
    p {
      font-weight: 300;
      &:not(:first-child) {
        margin-top: 1.375em;
      }
    }
  }
}
.purchase-info3 {
  padding: 3.125em 0;
  &__inner {
    text-align: center;
  }
  .purchase-evaluation__item {
    &-number {
      color: #B88571;
    }
    &-text {
      a {
        font-weight: 600;
        color: #B88571;
        &:hover {
          color: #676766;
        }
      }
    }
  }
  &__desc {
    margin-top: 3.125em;
    p {
      font-weight: 300;
      span {
        font-weight: 600;
      }
      &:not(:first-child) {
        margin-top: 1.375em;
      }
    }
  }
}
.purchase-connection2 {
  padding-bottom: 6.25em;
}

@media screen and (max-width: 992px) {
  .purchase-main {
    padding: 3.125em 0 1.5625em;
    &__content {
      padding: 2em;
    }
    &__flex {
      display: block;
    }
    &__list {
      width: 100%;
      margin: 0 0 2em 0;
    }

  }
  .purchase-info {
    padding: 1.5625em 0;
    &__items {
      grid-template-columns: repeat(2, calc(50% - 1.25em / 2));
      gap: 1.25em;
    }
    .border {
      padding: 1.5625em 1em;
    }
  }
  .purchase-info2 {
    padding: 1.5625em 0;
    &__subtitle {
      margin-top: 1.25em;
    }
    &__items {
      display: grid;
      grid-template-columns: repeat(3, calc(100% / 3 - 2.5em / 3));
      gap: 1.25em;
      width: 100%;
      margin-top: 2.5em;
    }
    &__item {
      margin: 0;
    }
  }
  .purchase-evaluation {
    padding: 1.5625em 0;
    &__subtitle {
      margin-top: 1.25em;
    }
    &__items {
      grid-template-columns: repeat(2, calc(50% - 6.25em / 2));
      gap: 3.125em 6.25em;
      margin-top: 2.5em;
    }
  }
  .purchase-connection {
    padding: 1.5625em 0;
    &__inner {
      padding: 2em;
    }
    &__form {
      display: block;
      &-group {
        &:not(:first-child) {
          margin: 2em 0 0 0;
        }
      }
      &-btn {
        width: 21.625em;
        margin: 2em 0 0 0;
      }
    }
  }
  .purchase-desc {
    padding: 1.5625em 0;
  }
  .purchase-info3 {
    padding: 1.5625em 0;
    &__desc {
      margin-top: 2.5em;
    }
  }
  .purchase-connection2 {
    padding-bottom: 3.125em;
  }
}

@media screen and (max-width: 768px) {
  .purchase-info2 {
    &__items {
      grid-template-columns: repeat(2, calc(50% - 1.25em / 2));
    }
  }
}

@media screen and (max-width: 576px) {
  .purchase-main {
    &__content {
      padding: 2em 1em;
    }
    &__btn {
      width: 100%;
    }
  }
  .purchase-info {
    &__items {
      grid-template-columns: 100%;
    }
    &__item {
      &:nth-child(1) {
        order: -4;
      }
      &:nth-child(2) {
        order: -3;
      }
      &:nth-child(3) {
        order: -1;
      }
      &:nth-child(4) {
        order: -2;
      }
    }
  }
  .purchase-info2 {
    &__items {
      grid-template-columns: 100%;
    }
  }
  .purchase-evaluation {
    &__items {
      grid-template-columns: 100%;
      gap: 6.25em;
    }
    &__item {
      &-icon {
        top: unset;
        right: unset;
        left: 50%;
        transform: translate(-50%, 0) rotate(90deg);
        bottom: -4.0625em;
      }
    }
  }
  .purchase-connection {
    &__inner {
      padding: 2em 1em;
    }
    &__form {
      &-group {
        input {
          width: 100%;
        }
      }
      &-btn {
        width: 100%;
      }
    }
  }
}