.btn-red {
  background: #C98B94;
  border: 0.0625em solid #c98b94;
  box-shadow: 0 0 0.625em 0 rgba(112, 112, 112, 0.30);
  color: #ffffff;
  &:hover {
    border: 0.0625em solid #c98b94;
    background: transparent;
    color: #676766;
  }
}
.btn-white {
  position: fixed;
  z-index: 4;
  top: 50%;
  left: 0.625em;
  transform: translate(0, -50%);
  width: 2.625em;
  flex-direction: column;
  background: #ffffff;
  border: 0.0625em solid #2C4158;
  border-radius: 0.625em;
  padding: 1em 0;
  span {
    font-size: 1.125em;
    font-weight: 300;
    color: #2C4158;
    writing-mode: vertical-rl;
    transform: rotate(180deg);
  }
  i {
    flex-shrink: 0;
    width: 2.5em;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 0.625em;
    svg {
      width: 100%;
      height: auto;
    }
  }
  &:hover {
    background: #2C4158;
    span {
      color: #ffffff;
    }
    i svg path {
      fill: #ffffff;
    }
  }
}
.breadcrumbs {
  &__items {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
  &__item {
    display: flex;
    align-items: center;
    font-size: 1.5625em;
    line-height: 120%;
    margin: 0 1.5625em 1.5625em 0;
    span:nth-child(1) {
      margin-right: 1.5625em;
    }
    &:hover {
      color: #b88571;
    }
    &--active {
      &:hover {
        color: #676766;
      }
    }
  }
}
.wrap {
  position: relative;
  overflow: hidden;
}
.swiper-button-prev {
  z-index: 1;
  width: auto;
  &:after {
    display: none;
  }
}
.swiper-button-next {
  z-index: 1;
  width: auto;
  &:after {
    display: none;
  }
}
.lines-top {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(0, -50%);
  width: 100%;
  height: 0.125em;
  display: block;
  background: #C98B94;
  border-radius: 0.1875em;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 0.125em;
    height: 2.8125em;
    background: #C98B94;
    border-radius: 0.1875em;
  }
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 0.125em;
    height: 2.8125em;
    background: #C98B94;
    border-radius: 0.1875em;
  }
}
.lines-bottom {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(0, -50%);
  width: 100%;
  height: 0.125em;
  display: block;
  background: #C98B94;
  border-radius: 0.1875em;
  &:after {
    content: '';
    position: absolute;
    right: 0;
    bottom: 0;
    width: 0.125em;
    height: 2.8125em;
    background: #C98B94;
    border-radius: 0.1875em;
  }
  &:before {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 0.125em;
    height: 2.8125em;
    background: #C98B94;
    border-radius: 0.1875em;
  }
}
.form {
  &-group {
    padding: 0;
    margin: 0;
    border: none;
    input {
      width: 100%;
      height: 3.4375em;
      border: 0.0625em solid #676766;
      border-radius: 1.71875em;
      font-size: 1em;
      line-height: 120%;
      color: #676766;
      padding: 0 1.125em;
      &::placeholder {
        color: #BABABA;
      }
    }
    textarea {
      width: 100%;
      height: 8.75em;
      border: 0.0625em solid #676766;
      border-radius: 1.71875em;
      font-size: 1em;
      line-height: 120%;
      color: #676766;
      padding: 1.125em;
      resize: none;
      &::placeholder {
        color: #BABABA;
      }
    }
  }
  .form-file {
    position: relative;
    input {
      position: relative;
      z-index: 2;
      opacity: 0;
    }
    &__input {
      span {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 3.4375em;
        display: flex;
        align-items: center;
        border: 0.0625em solid #676766;
        border-radius: 1.71875em;
        font-size: 1em;
        line-height: 120%;
        color: #BABABA;
        padding: 0 2.5em 0 1.125em;
      }
      img {
        position: absolute;
        top: 50%;
        right: 1.6875em;
        transform: translate(0, -50%);
        width: 0.8125em;
      }
    }
  }
  &-check {
    position: relative;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      opacity: 0;
      cursor: pointer;
      &:checked ~ label:before {
        opacity: 1;
        pointer-events: all;
      }
    }
    label {
      display: inline-block;
      font-weight: 600;
      padding-left: 2.0625em;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 1.4365em;
        height: 1.4365em;
        border: 0.0625em solid #676766;
        border-radius: 0.3125em;
      }
      &:before {
        content: '';
        position: absolute;
        top: 0.375em;
        left: 0.4375em;
        width: 0.75em;
        height: 0.625em;
        background-image: url("../img/check_small.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: 0.75em 0.625em;
        opacity: 0;
        pointer-events: none;
        transition: all 0.5s;
      }
    }
  }
  &-btn {
    width: 100%;
    height: 4.125em;
    border-radius: 6.26em;
    box-shadow: 0 0 0.625em 0 rgba(112, 112, 112, 0.30);
    span {
      font-weight: 700;
    }
  }
}
.modal {
  background: rgba(0, 0, 0, 0.5);;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  overflow-y: auto;
  z-index: 1000;
  opacity: 0;
  pointer-events: none;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    width: 86.875em;
    background: #ffffff;
    border: 0.0625em solid #D9AE9D;
    max-width: 100%;
    border-radius: 1.875em;
    padding: 3.125em;
    position: relative;
    margin: 0 auto;
    @media screen and (max-width: 992px) {
      margin: 50px auto;
      max-width: calc(100% - 30px);
    }
  }
  &__logo {
    display: block;
    width: 17.25em;
    margin: 0 auto 1.6875em;
    img {
      width: 100%;
    }
  }
  &__close {
    position: absolute;
    top: 1.4375em;
    right: 2.3125em;
    background: transparent;
    padding: 0;
    margin: 0;
    span {
      color: #E5C8BC;
    }
  }
}
.nav {
  padding: 3.125em 0;
  &__list {
    li {
      font-size: 1.25em;
      line-height: 140%;
      &:not(:first-child) {
        margin-top: 1em;
      }
      &:hover {
        color: #C98B94;
      }
    }
  }
  &__btn {
    width: 15.625em;
    height: 3.125em;
    border-radius: 6.25em;
  }
}


