  }
  .btn-white {
    top: unset;
    left: 50%;
    bottom: 1.25em;
    transform: translate(-50%, 0) rotate(-180deg);
    width: 19.375em;
    height: 2.625em;
    flex-direction: row;
    padding:  0 1em;
    span {
      writing-mode: unset;
    }
    i {
      margin: 0 0 0 0.625em;
    }
  }
  .lines-top {
    display: none;
  }
  .wrap {
    padding-bottom: 5em;
  }
  .lines-bottom {
    display: none;
  }
  .modal {
    &__dialog {
      padding: 3.125em 1em 2em 1em;
    }
    &__logo {
      width: 12.5em;
    }
  }
}

