.advantages {
  padding: 6.25em 0 3.125em;
  overflow: hidden;
  &__top,
  &__bottom {
    position: relative;
  }
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    span {
      color: #2C4158;
      background: #ffffff;
      text-transform: uppercase;
      padding: 0 2em;
    }
  }
  &__content {
    margin: 3.75em 0;
  }
  &__subtitle {
    text-align: center;
  }
  &__cards {
    width: calc(100% + 3.125em);
    display: flex;
    flex-wrap: wrap;
    margin-top: 4.375em;
  }
  &__desc {
    text-align: center;
    padding: 2.125em 0;
    position: relative;
    margin-top: 5em;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 50%;
      transform: translate(-50%, 0);
      width: 46.875em;
      height: 0.0625em;
      background: #b88571;
    }
    &:before {
      content: '';
      position: absolute;
      left: 50%;
      bottom: 0;
      transform: translate(-50%, 0);
      width: 46.875em;
      height: 0.0625em;
      background: #b88571;
    }
    p span {
      color: #b88571;
    }
  }
  &__btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 33.125em;
    height: 4.125em;
    border-radius: 6.25em;
    margin: 3.75em auto 0;
  }
}
.advantage-card {
  flex: 1 0 calc(100% / 3 - 3.125em);
  border: 0.0625em solid #C98B94;
  border-radius: 1.875em;
  padding: 2.5em;
  margin: 0 3.125em 4.375em 0;
  &__icon {
    width: 6.875em;
    img {
      width: 100%;
    }
  }
  &__text {
    margin-top: 2.5em;
    p span {
      font-weight: 600;
      color: #b88571;
    }
  }
}
.purchase2 {
  padding: 3.125em 0;
  &__top,
  &__bottom {
    position: relative;
  }
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    span {
      color: #2C4158;
      background: #ffffff;
      text-transform: uppercase;
      padding: 0 2em;
    }
  }
  &__content {
    margin: 3.75em 0;
  }
  &__text {
    p {
      &:not(:first-child) {
        margin-top: 1.375em;
      }
      a {
        color: #b88571;
        text-decoration: underline;
        text-underline-offset: 0.1875em;
      }
      span {
        color: #b88571;
      }
      .text-decoration {
        text-decoration: line-through;
      }
    }
  }
  &__list {
    background: #ffebe4;
    border-radius: 1.875em;
    padding: 2.5em;
    margin: 1.375em 0;
    li {
      display: flex;
      &:not(:first-child) {
        margin-top: 0.3125em;
      }
      i {
        flex-shrink: 0;
        width: 2.375em;
        margin-top: 0.3125em;
        margin-right: 1em;
        img {
          width: 100%;
        }
      }
      span a {
        color: #b88571;
      }
    }
  }
}
.reviews {
  padding: 3.125em 0;
  &__top,
  &__bottom {
    position: relative;
  }
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    span {
      color: #2C4158;
      background: #ffffff;
      text-transform: uppercase;
      padding: 0 2em;
    }
  }
  &__content {
    padding: 0 3em;
    margin: 5em 0;
  }
  &__sites {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
  &__site {
    display: flex;
    align-items: center;
    margin: 0 0 1.375em 0;
    &:not(:last-child) {
      margin-right: 1.375em;
    }
    &-icon {
      flex-shrink: 0;
      width: 1.875em;
      margin-right: 1em;
      img {
        width: 100%;
      }
    }
    &-text {
      display: flex;
      align-items: center;
      span {
        font-weight: 600;
        letter-spacing: 1px;
        &:not(:first-child) {
          margin-left: 0.625em;
        }
      }
    }
  }
  &__estimation {
    display: flex;
    align-items: center;
    margin-top: 2.375em;
    &-left {
      display: flex;
      align-items: center;
      margin-right: 1.25em;
    }
    &-right {
      p {
        color: #929292;
      }
    }
    &-text {
      display: flex;
      align-items: center;
      margin-right: 1em;
      span {
        font-weight: 600;
        letter-spacing: 1.2px;
        &:not(:first-child) {
          margin-left: 0.3125em;
        }
      }
    }
    &-icons {
      display: flex;
      align-items: center;
    }
    &-icon {
      width: 1.875em;
      svg {
        width: 100%;
        height: auto;
      }
    }
  }
  &__slider {
    position: relative;
    margin-top: 2.375em;
  }
  .swiper {
    overflow: unset;
  }
  &__slider-nav {
    display: flex;
    align-items: center;
    margin-top: 1.5625em;
  }
  &__slider-btns {
    display: flex;
    align-items: center;
    margin-right: 1.375em;
  }
  .swiper-button-prev {
    position: static;
    width: 1.876em;
    height: 1.875em;
    background: #E2E2E2;
    border-radius: 50%;
    margin-top: 0;
    svg {
      width: 0.5em;
      height: auto;
    }
  }
  .swiper-button-next {
    position: static;
    width: 1.876em;
    height: 1.875em;
    background: #E2E2E2;
    border-radius: 50%;
    margin-top: 0;
    margin-left: 0.75em;
    svg {
      width: 0.5em;
      height: auto;
    }
  }
  .swiper-pagination {
    position: static;
    height: 0.25em;
    .swiper-pagination-progressbar-fill {
      background: #6BA5F1;
    }
  }
  .swiper-slide {
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    &-active,
    &-next,
    &-next + .swiper-slide {
      opacity: 1;
      pointer-events: all;
    }
  }
}
.review-card {
  background: #FFF;
  border: 0.0625em solid transparent;
  box-shadow: 0 0 0.3125em 0 rgba(0, 0, 0, 0.10);
  border-radius: 0.625em;
  padding: 1.25em 0.875em;
  &:hover {
    border: 0.0625em solid #b88571;
  }
  &__top {
    display: flex;
    align-items: center;
  }
  &__img {
    flex-shrink: 0;
    width: 2.875em;
    margin-right: 0.625em;
    img {
      width: 100%;
    }
  }
  &__rating-group {
    display: flex;
    align-items: center;
    margin-top: 0.1875em;
  }
  &__rating {
    width: 1.25em;
    svg {
      width: 100%;
      height: auto;
    }
  }
  &__date {
    margin-top: 0.1875em;
    span {
      color: #929292;
    }
  }
  &__text {
    margin-top: 0.75em;
  }
  &__link {
    margin-top: 0.3125em;
    span {
      color: #929292;
      transition: all 0.5s;
      &:hover {
        color: #b88571;
      }
    }
  }
  &__site {
    margin-top: 0.875em;
    span {
      color: #929292;
      text-decoration: underline;
      text-underline-offset: 0.1875em;
      transition: all 0.5s;
      &:hover {
        color: #b88571;
      }
    }
  }
}
.images {
  padding: 3.125em 0 6.125em;
  &__top,
  &__bottom {
    position: relative;
  }
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    span {
      color: #2C4158;
      background: #ffffff;
      text-transform: uppercase;
      padding: 0 2em;
    }
  }
  &__content {
    margin: 6.25em 0;
  }
  .swiper {
    overflow: unset;
    padding: 0 3em;
  }
  .swiper-button-prev {
    left: 0;
  }
  .swiper-button-next {
    right: 0;
  }
  .swiper-slide {
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    &-active,
    &-next,
    &-next + .swiper-slide {
      opacity: 1;
      pointer-events: all;
    }
    img {
      width: 100%;
    }
  }
}

@media screen and (max-width: 992px) {
  .advantages {
    padding: 3.125em 0;
    &__title {
      span {
        padding: 0;
      }
    }
    &__content {
      margin: 2.5em 0 0 0;
    }
    &__cards {
      width: 100%;
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 1.875em / 2));
      gap: 1.875em;
      margin-top: 2.5em;
    }
    &__desc {
      padding: 1.25em;
      margin-top: 2.5em;
      &:after {
        width: 100%;
      }
      &:before {
        width: 100%;
      }
    }
  }
  .advantage-card {
    padding: 1.875em 1em;
    margin: 0;
    &__icon {
      width: 3.75em;
    }
  }
  .purchase2 {
    padding: 1.5625em 0;
    &__title {
      span {
        padding: 0;
      }
    }
    &__content {
      margin: 2.5em 0 0 0;
    }
    &__list {
      padding: 1.875em 1.25em;
      li i {
        margin-top: 0;
      }
    }
  }
  .reviews {
    padding: 1.5625em 0;
    &__content {
      padding: 0;
      margin: 2.5em 0 0 0;
    }
  }
  .images {
    padding: 1.5625em 0 3.125em;
    &__content {
      margin: 2.5em 0 0 0;
    }
  }
}

@media screen and (max-width: 768px) {
  .reviews {
    .swiper-slide {
      &-next + .swiper-slide {
        opacity: 0;
        pointer-events: none;
      }
    }
  }
  .images {
    .swiper-slide {
      &-next + .swiper-slide {
        opacity: 0;
        pointer-events: none;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .advantages {
    &__btn {
      width: 100%;
      margin-top: 2.5em;
    }
  }
  .reviews {
    &__estimation {
      display: block;
      &-left {
        margin: 0 0 0.625em 0;
      }
    }
    .swiper-slide {
      &-next {
        opacity: 0;
        pointer-events: none;
      }
    }
  }
  .images {
    .swiper-slide {
      &-next {
        opacity: 0;
        pointer-events: none;
      }
    }
  }
}

@media screen and (max-width: 400px) {
  .advantages {
    &__cards {
      grid-template-columns: 100%;
    }
  }
}