.header {
  padding-top: 5em;
  position: relative;
  &__logo {
    position: absolute;
    top: 5em;
    left: 50%;
    transform: translate(-50%, 0);
    width: 35.125em;
    img {
      width: 100%;
    }
  }
}
.header-top {
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__right {
    display: flex;
  }
  &__logo {
    display: none;
  }
  &__tel {
    display: block;
    &:hover {
      color: #C98B94;
    }
  }
  &__socials {
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
  &__social {
    width: 2.625em;
    height: 2.625em;
    display: flex;
    justify-content: center;
    align-items: center;
    background: transparent;
    border: 0.0625em solid #676766;
    border-radius: 50%;
    &:not(:first-child) {
      margin-left: 0.625em;
    }
    svg {
      width: 1.5em;
      height: auto;
    }
    &:hover {
      background: #C98B94;
      border: 0.0625em solid #C98B94;
      svg path {
        stroke: #ffffff;
      }
    }
  }
  &__btn {
    height: 3.25em;
    border-radius: 1.625em;
    padding: 0 1.5625em;
    margin-top: 2em;
  }
}
.header-center {
  padding: 1.5625em 0 4.0625em;
  &__items {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__item {
    &:hover {
      color: #c98b94;
    }
  }
}
.header-bottom {
  padding: 4.0625em 0 0 0;
  &__list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-top: 0.0625em solid #C98B94;
    border-bottom: 0.0625em solid #C98B94;
    li {
      padding: 1.25em 2em;
      &:first-child {
        padding-left: 0;
      }
      &:last-child {
        padding-right: 0;
      }
    }
  }
  &__link {
    font-size: 1.375em;
    line-height: 120%;
    &:hover {
      color: #C98B94;
    }
  }
  &__item {
    padding: 0 !important;
    position: relative;
    z-index: 3;
    &--active {
      .header-bottom__item-show {
        background: #DADADA;
        color: #676766;
      }
      .header-bottom__item-hidden {
        opacity: 1;
        pointer-events: all;
      }
    }
    &-show {
      padding: 1.25em 2em;
      cursor: pointer;
      transition: all 0.5s;
      span {
        font-size: 1.375em;
        line-height: 120%;
      }
      i {
        display: none;
      }
    }
    &-hidden {
      position: absolute;
      top: 100%;
      left: 0;
      width: 25.4375em;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
    }
    &-hidden2 {
      left: unset;
      right: 0;
    }
  }
  &__question {
    position: relative;
    &--active {
      .header-bottom__question-show {
        background: #bbbbbb;
      }
      .header-bottom__question-hidden {
        opacity: 1;
        pointer-events: all;
      }
    }
    &-show {
      background: #DADADA;
      padding: 1em 1.5em;
      cursor: pointer;
      &:hover {
        background: #bbbbbb;
      }
      span {
        font-size: 1.375em;
        line-height: 120%;
        font-weight: 600;
        color: #676766;
        text-transform: uppercase;
      }
      i {
        display: none;
      }
    }
    &-hidden {
      position: absolute;
      top: 0;
      left: 100%;
      width: 100%;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
    }
    &-hidden2 {
      left: unset;
      right: 100%;
    }
    &-link {
      display: block;
      background: #DADADA;
      color: #ffffff;
      padding: 1.25em 2em;
      cursor: pointer;
      &:hover {
        background: #bbbbbb;
      }
      span {
        font-size: 1.375em;
        line-height: 120%;
        font-weight: 600;
        color: #676766;
        text-transform: uppercase;
      }
    }
  }
  &__items {
    display: none;
  }
  &__tel {
    display: none;
  }
  &__btn {
    display: none;
  }
}
.burger {
  position: relative;
  z-index: 10;
  width: 2.1875em;
  height: 1.5625em;
  cursor: pointer;
  display: none;
  margin-left: 2.5em;
  span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 0.1875em;
    background: #C98B94;
    border-radius: 0.1875em;
    transition: all 0.5s;
    &:nth-child(1) {
      top: 0;
    }
    &:nth-child(2) {
      top: 50%;
      transform: translate(0, -50%);
    }
    &:nth-child(3) {
      bottom: 0;
    }
  }
  &--active {
    span {
      background: #676766;
    }
    span:nth-child(1) {
      top: 0.6875em;
      transform: rotate(-45deg);
    }
    span:nth-child(2) {
      display: none;
    }
    span:nth-child(3) {
      bottom: 0.71875em;
      transform: rotate(45deg);
    }
  }
}
.search {
  position: fixed;
  z-index: 15;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  background: rgba(0,0,0,.5);
  padding: 9.375em 1.25em 3.125em;
  opacity: 0;
  pointer-events: none;
  transition: all 0.5s;
  &--active {
    opacity: 1;
    pointer-events: all;
  }
  &-group {
    width: 37.5em;
    height: 3.75em;
    display: flex;
    align-items: stretch;
    position: relative;
    input {
      width: 100%;
      height: 100%;
      padding: 0 1.25em;
      font-size: 1.25em;
      line-height: 120%;
      color: #000000;
      background: #ffffff;
      border: none;
      &::placeholder {
        color: #676766;
      }
    }
    button {
      flex-shrink: 0;
      width: 3.75em;
      background: #000000;
      padding: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      svg {
        width: 1.375em;
        height: auto;
        path {
          stroke: #ffffff;
        }
      }
    }
  }
}
.search-close {
  position: absolute;
  z-index: 2;
  top: 1.25em;
  right: 1.25em;
  width: 2.1875em;
  height: 2.1875em;
  display: flex;
  flex-direction: column;
  cursor: pointer;
  span {
    position: absolute;
    width: 100%;
    height: 0.1875em;
    background: #ffffff;
    border-radius: 0.1875em;
    &:nth-child(1) {
      top: 1em;
      transform: rotate(-45deg);
    }
    &:nth-child(2) {
      bottom: 1em;
      transform: rotate(45deg);
    }
  }
}


@media screen and (max-width: 992px) {
  .header {
    padding-top: 2.5em;
    &__logo {
     display: none;
    }
  }
  .header-top {
    &__inner {
      align-items: center;
    }
    &__right {
      align-items: center;
    }
    &__logo {
      display: block;
      width: 12.5em;
      img {
        width: 100%;
      }
    }
    &__tel {
      display: none;
    }
    &__btn {
     display: none;
    }
  }
  .header-center {
   display: none;
  }
  .header-bottom {
    padding: 0;
    &__menu {
      position: fixed;
      z-index: 8;
      top: 0;
      left: -105%;
      width: 100%;
      height: 100%;
      background: #DADADA;
      padding: 9.375em 1.25em 3.125em;
      transition: all 0.5s;
      overflow-y: auto;
      &::-webkit-scrollbar {
        width: 0.1875em;
      }
      &::-webkit-scrollbar-track {
        background: #ffffff;
        border-radius: 0.1875em;
      }
      &::-webkit-scrollbar-thumb {
        background: #9D626A;
        border-radius: 0.1875em;
      }
      &--active {
        left: 0;
      }
    }
    &__list {
      flex-direction: column;
      border: none;
      li {
        padding: 0;
        &:not(:first-child) {
          margin-top: 1.25em;
        }
      }
    }
    &__link {
      font-size: 1em;
      color: #676766;
      &:hover {
        color: #676766;
        opacity: 0.6;
      }
    }
    &__item {
      text-align: center;
      &--active {
        .header-bottom__item-show {
          opacity: 0.6;
          background: transparent;
          i {
            transform: rotate(-180deg);
          }
        }
        .header-bottom__item-hidden {
          display: block;
        }
      }
      &-show {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0;
        color: #676766;
        span {
          font-size: 1em;
        }
        i {
          display: block;
          flex-shrink: 0;
          width: 0.5em;
          margin-left: 1em;
          transition: all 0.3s;
          svg {
            transform: rotate(90deg);
            width: 100%;
            height: auto;
            path {
              fill: #676766;
            }
          }
        }
        &:hover {
          opacity: 0.8;
        }
      }
      &-hidden {
        display: none;
        position: static;
        width: auto;
        opacity: 1;
        pointer-events: all;
        margin-top: 1.25em;
      }
    }
    &__question {
      &--active {
        .header-bottom__question-show {
          opacity: 0.6;
          background: transparent;
          i {
            transform: rotate(-180deg);
          }
        }
        .header-bottom__question-hidden {
          display: block;
        }
      }
      &:not(:first-child) {
        margin: 1.25em 0 0 0;
      }
      &-show {
        display: flex;
        justify-content: center;
        align-items: center;
        background: transparent;
        padding: 0;
        transition: all 0.5s;
        span {
          font-size: 1em;
        }
        i {
          display: block;
          flex-shrink: 0;
          width: 0.5em;
          margin-left: 1em;
          transition: all 0.3s;
          svg {
            transform: rotate(90deg);
            width: 100%;
            height: auto;
            path {
              fill: #676766;
            }
          }
        }
        &:hover {
          background: transparent;
          opacity: 0.6;
        }
      }
      &-hidden {
        display: none;
        position: static;
        width: auto;
        opacity: 1;
        pointer-events: all;
        margin-top: 1.25em;
      }
      &-link {
        background: transparent;
        padding: 0;
        &:not(:first-child) {
          margin-top: 1.25em;
        }
        span {
          font-size: 1em;
        }
        &:hover {
          background: transparent;
          opacity: 0.5;
        }
      }
    }
    &__items {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      margin-top: 1.25em;
    }
    &__item {
      color: #676766;
      &:not(:first-child) {
        margin-top: 1.25em;
      }
    }
    &__tel {
      display: flex;
      justify-content: center;
      align-items: center;
      color: #676766;
      margin-top: 1.25em;
      a {
        &:hover {
          opacity: 0.5;
        }
      }
    }
    &__btn {
      display: flex;
      height: 3.25em;
      border-radius: 1.625em;
      padding: 0 1.5625em;
      margin: 1.25em auto 0;
      span {
        color: #ffffff;
      }
      &:hover {
      span {
        color: #676766;
      }
      }
    }
  }
  .burger {
    display: block;
  }
}

@media screen and (max-width: 576px) {
  .header-top {
    &__logo {
      width: 9.375em;
    }
    &__social {
      width: 2em;
      height: 2em;
      svg {
        width: 1em;
      }
    }
  }
  .burger {
    margin-left: 1.25em;
  }
  .search {
    &-group {
      width: 100%;
    }
  }
}
