.contacts-main {
  padding: 6.25em 0 3.125em;
  &__inner {
    text-align: center;
  }
  &__top,
  &__bottom {
    position: relative;
  }
  &__title {
   text-align: center;
    span {
      position: relative;
      z-index: 2;
      background: #ffffff;
      color: #2C4158;
      text-transform: uppercase;
      padding: 0 2em;
    }
  }
  &__content {
    margin: 2.5em 0;
  }
  &__location {
    display: flex;
    justify-content: center;
    align-items: center;
    i {
      flex-shrink: 0;
      width: 1.25em;
      margin-right: 0.875em;
    }
  }
  &__flex {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 1.125em;
  }
  &__tel {
    span {
      font-size: 2.5em;
      line-height: 120%;
      font-weight: 600;
      color: #B88571;
    }
  }
  &__socials {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-left: 2.5em;
  }
  &__social {
    width: 2.625em;
    height: 2.625em;
    display: flex;
    justify-content: center;
    align-items: center;
    background: transparent;
    border: 0.0625em solid #B88571;
    border-radius: 50%;
    &:not(:first-child) {
      margin-left: 0.625em;
    }
    svg {
      width: 1.5em;
      height: auto;
      path {
        fill: #B88571;
      }
    }
    &:hover {
      background: #B88571;
      svg path {
        fill: #ffffff;
      }
    }
  }
  .map {
    height: 37.5em;
    margin-top: 2.5em;
  }
  &__info {
    margin-top: 2.125em;
  }
}
.contacts-connection {
  padding: 3.125em 0 6.25em;
  &__top,
  &__bottom {
    position: relative;
  }
  &__title {
    text-align: center;
    span {
      position: relative;
      z-index: 2;
      background: #ffffff;
      color: #2C4158;
      text-transform: uppercase;
      padding: 0 2em;
    }
  }
  &__content {
    margin: 3.75em 0;
  }
  .connection__form {
    margin: 0 auto;
    &-group {
      input {
        border-radius: 0.875em;
      }
      textarea {
        border-radius: 1.3125em;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .contacts-main {
    padding: 3.125em 0 1.5625em;
    &__title {
      span {
        padding: 0;
      }
    }
    &__content {
      margin: 2.5em 0 0 0;
    }
    &__tel {
      span {
        font-size: 2.125em;
      }
    }
  }
  .contacts-connection {
    padding: 1.5625em 0 3.125em;
    &__title {
      span {
        padding: 0;
      }
    }
    &__content {
      margin: 2.5em 0 0 0;
    }
  }
}

@media screen and (max-width: 576px) {
  .contacts-main {
    &__flex {
      flex-direction: column;
    }
    &__socials {
      margin: 0.75em 0 0 0;
    }
  }
}