.evaluation-main {
  padding: 6.25em 0 3.125em;
  &__top,
  &__bottom {
    position: relative;
  }
  &__top  {
    .evaluation-main__title {
      span {
        letter-spacing: 0.0625em;
      }
    }
  }
  &__title {
     text-align: center;
     position: relative;
     z-index: 2;
     span {
       font-weight: 400;
       background: #ffffff;
       padding: 0 0.9375em;
     }
   }
  &__content {
    margin: 3.125em 0;
  }
  &__text {
    text-align: center;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 9.375em / 4));
    gap: 3.125em;
    margin-top: 3.125em;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 3.125em;
      left: 0;
      width: calc(100% - 14.375em);
      height: 0.125em;
      background: #B88571;
    }
  }
  &__item {
    position: relative;
    z-index: 2;
    &-number {
      width: 6.25em;
      height: 6.25em;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #ffffff;
      border: 0.125em solid #B88571;
      border-radius: 50%;
      span {
        font-size: 2.34375em;
        line-height: 120%;
        font-weight: 700;
        color: #B88571;
      }
    }
    &-text {
      margin-top: 3.75em;
      p {
        a {
          color: #B88571;
          &:hover {
            color: #676766;
          }
        }
      }
    }
  }
}
.evaluation-info {
  padding: 3.125em 0;
  overflow: hidden;
  &__top,
  &__bottom {
    position: relative;
  }
  &__title {
    text-align: center;
    position: relative;
    z-index: 2;
    span {
      font-weight: 400;
      background: #ffffff;
      padding: 0 0.9375em;
    }
  }
  &__content {
    margin: 3.125em 0;
  }
  &__text {
    text-align: center;
    p {
      font-weight: 600;
    }
  }
  &__items {
    display: flex;
    flex-wrap: wrap;
    width: calc(100% + 3.125em);
    margin-top: 3.125em;
  }
  &__item {
    flex: 1 0 calc(100% / 3 - 3.125em);
    margin: 0 3.125em 3.125em 0;
    border: 0.0625em solid #C98B94;
    border-radius: 1.875em;
    position: relative;
    padding: 2.5em;
    &-img {
      img {
        position: absolute;
        top: -0.0625em;
        left: -0.0625em;
        width: calc(100% + 0.125em);
        height: calc(100% + 0.125em);
        border-radius: 1.875em;
      }
    }
    &-icon {
      width: 6.25em;
      img {
        width: 100%;
      }
    }
    &-text {
      margin-top: 1.375em;
      p a {
        font-weight: 600;
        color: #B88571;
      }
    }
  }
}
.evaluation-info2 {
  padding: 3.125em 0;
  overflow: hidden;
  &__inner {
    text-align: center;
  }
  &__top,
  &__bottom {
    position: relative;
  }
  &__title {
    text-align: center;
    position: relative;
    z-index: 2;
    span {
      font-weight: 400;
      background: #ffffff;
      padding: 0 0.9375em;
    }
  }
  &__content {
    margin: 3.125em 0;
  }
  &__items {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: calc(100% + 11.25em);
    margin-top: 3.125em;
  }
  &__item {
    max-width: 20.125em;
    flex: 1 0 calc(100% / 3 - 11.25em);
    margin: 0 11.25em 3.75em 0;
    &-number {
      span {
        display: inline-block;
        font-size: 8.5625em;
        line-height: 105%;
        font-weight: 600;
        color: #B88571;
        position: relative;
        &:after {
          content: '';
          position: absolute;
          left: 50%;
          bottom: 0.0625em;
          transform: translate(-50%, 0);
          width: 100%;
          height: 5px;
          background: #B88571;
        }
      }
    }
    &-text {
      margin-top: 1em;
      p a {
        color: #B88571;
        text-decoration: underline;
        text-underline-offset: 0.3125em;
        &:hover {
          color: #676766;
        }
      }
    }
  }
  &__text {
    max-width: 62.3125em;
    margin: 0 auto;
    p a {
      font-weight: 600;
      color: #B88571;
      &:hover {
        color: #676766;
      }
    }
  }
}
.evaluation-connection {
  padding-bottom: 6.25em;
}

@media screen and (max-width: 992px) {
  .evaluation-main {
    padding: 3.125em 0 1.5625em;
    &__title {
      span {
        padding: 0;
      }
    }
    &__content {
      margin: 2.5em 0;
    }
    &__items {
      grid-template-columns: repeat(2, calc(50% - 1.875em / 2));
      gap: 1.875em;
      margin-top: 2.5em;
      &:after {
        display: none;
      }
    }
    &__item {
      &-number {
        width: 3.75em;
        height: 3.75em;
        span {
          font-size: 2em;
        }
      }
      &-text {
        margin-top: 1.25em;
      }
    }
  }
  .evaluation-info {
    padding: 1.5625em 0;
    &__title {
      span {
        padding: 0;
      }
    }
    &__content {
      margin: 2.5em 0 0 0;
    }
    &__items {
      width: calc(100% + 1.875em);
      margin-top: 2.5em;
    }
    &__item {
      flex: 1 0 calc(100% / 3 - 1.875em);
      margin: 0 1.875em 1.875em 0;
      padding: 1.875em 1.25em;
      &-icon {
        width: 3.75em;
      }
    }
  }
  .evaluation-info2 {
    padding: 1.5625em 0;
    &__title {
      span {
        padding: 0;
      }
    }
    &__content {
      margin: 2.5em 0 0 0;
    }
    &__items {
      width: calc(100% + 2.5em);
      margin-top: 2.5em;
    }
    &__item {
      flex: 1 0 calc(100% / 3 - 2.5em);
      margin: 0 2.5em 2.5em 0;
      &-number {
        span {
          font-size: 3.75em;
        }
      }
    }
  }
  .evaluation-connection {
    padding-bottom: 3.125em;
  }
}

@media screen and (max-width: 768px) {
  .evaluation-info {
    &__items {
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 1.875em / 2));
      gap: 1.875em;
      width: 100%;
    }
    &__item {
      margin: 0;
    }
  }
  .evaluation-info2 {
    &__item {
      flex: 1 0 calc(50% - 2.5em);
    }
  }
}

@media screen and (max-width: 576px) {
  .evaluation-main {
    &__items {
      grid-template-columns: 100%;
    }
  }
  .evaluation-info2 {
    &__items {
      width: 100%;
    }
    &__item {
      flex:  1 0 100%;
      margin: 0;
      &:not(:first-child) {
        margin-top: 1.875em;
      }
    }
    &__text {
      margin-top: 2.5em;
    }
  }
}

@media screen and (max-width: 400px) {
  .evaluation-info {
    &__items {
      grid-template-columns: 100%;
    }
    &__item {
      min-height: 12.5em;
    }
  }
}
