.services {
  padding: 6.25em 0;
  &__top,
  &__bottom {
    position: relative;
  }
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    span {
      color: #2C4158;
      text-transform: uppercase;
      background: #ffffff;
      padding: 0 2em;
    }
  }
  &__content {
    margin: 6.25em 0;
  }
  &__text {
    text-align: center;
    p {
      &:not(:first-child) {
        margin-top: 1.375em;
      }
      a {
        color: #B88571;
      }
    }
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 3.125em / 2));
    gap: 9.375em 3.125em;
    margin-top: 4.375em;
  }
}
.service-card {
  padding: 1.875em 2.5em;
  border-radius: 1.875em;
  position: relative;
  &:nth-child(3),
  &:nth-child(5),
  &:nth-child(6),
  &:nth-child(7){
    margin-top: -6.25em;
  }
  &:nth-child(6) {
    height: calc(100% + 12.5em);
  }
  &__link {
    margin-top: 0.3125em;
    &:hover {
      span {
        color: #B88571;
      }
    }
  }
  &__text {
    margin-top: 1.25em;
    p {
      &:not(:first-child) {
        margin-top: 1.375em;
      }
      a {
        text-decoration: underline;
        text-underline-offset: 0.1875em;
        &:hover {
          color: #B88571;
        }
      }
    }
  }
}
.bg-white {
  border: 0.0625em solid #C98B94;
  background: #ffffff;
  position: relative;
  .service-card__title {
    span {
      color: #B88571;
    }
  }
  .service-card__link {
    span {
      color: #DEB5B5;
      transition: all 0.5s;
    }
  }
}
.bg-gray {
  height: calc(100% + 6.26em);
  background: #F4F3F7;
  .service-card__title {
    span {
      color: #2C4158;
    }
  }
  .service-card__link {
    span {
      color: #AFBAC6;
      transition: all 0.5s;
    }
  }
}

@media screen and (max-width: 992px) {
  .services {
    padding: 3.125em 0;
    &__content {
      margin: 2.5em 0 0 0;
    }
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 1.875em / 2));
      gap: 1.875em;
      margin-top: 2.5em;
    }
  }
  .service-card {
    height: 100% !important;
    margin: 0 !important;
    padding: 1.875em 1em;
  }
}

@media screen and (max-width: 768px) {
  .services {
    &__cards {
      grid-template-columns: 100%;
    }
  }
  .bg-white {
    background: #F4F3F7;
    border: unset;
    .service-card__title {
      span {
        color: #2C4158;
      }
    }
    .service-card__link {
      span {
        color: #AFBAC6;
        transition: all 0.5s;
      }
    }
  }
}
