.product-main {
  padding: 3.125em 0;
  &__content {
    display: flex;
    justify-content: space-between;
    margin-top: 1.875em;
  }
  &__left {
    width: 40em;
    margin-right: 6.25em;
  }
  &__right {
    width: calc(100% - 46.25em);
  }
  .gallery {
    &__big {
      overflow: unset;
      .swiper-slide {
        opacity: 0;
        pointer-events: none;
        transition: all 0.3s;
        &-active {
          opacity: 1;
          pointer-events: all;
        }
      }
    }
    &__small {
      margin-top: 3em;
    }
  }
  .swiper-slide {
    img {
      display: block;
      width: 100%;
      height: 100%;
    }
  }
  .swiper-button-prev {
    left: -2.5em;
    width: 5em;
    height: 5em;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #b88571;
    border-radius: 50%;
    &:after {
      display: none;
    }
    svg {
      width: 1.625em;
      height: auto;
    }
  }
  .swiper-button-next {
    right: -2.5em;
    width: 5em;
    height: 5em;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #b88571;
    border-radius: 50%;
    &:after {
      display: none;
    }
  }
  &__desc-top {
    padding-bottom: 2.375em;
    border-bottom: 0.0625em solid #2C4158;
  }
  &__desc-bottom {
    padding-top: 2.375em;
  }
  &__text {
    margin-top: 1.5625em;
    p a {
      color: #b88571;
      &:hover {
        color: #676766;
      }
    }
  }
  &__title {
    display: flex;
    .text32 {
      color: #2C4158;
      text-transform: uppercase;
    }
    .product-card2__favorite {
      flex-shrink: 0;
      margin-top: 0.625em;
    }
  }
  &__info {
    margin-top: 1.5em;
  }
  &__article {
    display: flex;
    align-items: center;
    span:nth-child(1) {
      max-width: 4.3em;
      width: 100%;
    }
    span:nth-child(2) {
      color: #B88571;
      margin-left: 1em;
    }
  }
  &__availability {
    display: flex;
    align-items: center;
    margin-top: 0.3125em;
    span:nth-child(1) {
      max-width: 4.3em;
      width: 100%;
    }
    span:nth-child(2) {
      color: #B88571;
      margin-left: 1em;
    }
  }
  &__info {
    margin-top: 0.625em;
  }
  &__list {
    li {
      display: flex;
      justify-content: space-between;
      padding-bottom: 0.75em;
      border-bottom: 0.0625em solid #FFEBE4;
      &:not(:first-child) {
        margin-top: 0.3125em;
      }
      span:nth-child(2) {
        color: #B88571;
        text-align: right;
        margin-left: 2em;
      }
    }
  }
  &__price {
    display: flex;
    align-items: center;
    margin-top: 1.5em;
  }
  .new-price {
    span {
      font-size: 3.125em;
      line-height: 120%;
      font-weight: 600;
      color: #B88571;
    }
  }
  .old-price {
    margin-left: 2.1875em;
    position: relative;
    span:nth-child(1) {
      font-weight: 600;
      color: #B88571;
      text-decoration: line-through;
    }
    span:nth-child(2) {
      position: absolute;
      top: -1em;
      right: -3.125em;
      font-size: 0.875em;
      line-height: 120%;
      font-weight: 600;
      color: #FF4B4B;
      background: #FFEF63;
      border-radius: 0.375em;
      padding: 0.25em 0.75em;
    }
  }
  &__btns {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 3.125em / 2));
    gap: 3.125em;
    margin-top: 2.1875em;
  }
  &__btn-wrap {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__btn {
    width: 100%;
    height: 4.8125em;
    border-radius: 6.25em;
    span {
      font-weight: 700;
    }
  }
  &__btn2 {
    height: 4.8125em;
    background: #ffffff;
    border: 0.1875em solid #C98B94;
    box-shadow: 0 0 0.625em 0 rgba(112, 112, 112, 0.30);
    border-radius: 6.25em;
    &:hover {
      background: #C98B94;
      span {
        color: #ffffff;
      }
    }
    span {
      font-weight: 700;
      color: #C98B94;
    }
  }
  .delivery-time {
    margin-top: 0.1875em;
  }
}

@media screen and (max-width: 992px) {
  .product-main {
    padding: 3.125em 0 1.5625em;
    &__left {
      width: 25em;
      margin-right: 3.125em;
    }
    &__right {
      width: calc(100% - 28.125em);
    }
    .gallery {
      &__small {
        margin-top: 1.25em;
      }
    }
    .swiper-button-prev {
      left: 0.625em;
      width: 3.125em;
      height: 3.125em;
      svg {
        width: 1.5em;
      }
    }
    .swiper-button-next {
      right: 0.625em;
      width: 3.125em;
      height: 3.125em;
      svg {
        width: 1.5em;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .product-main {
    &__content {
      display: block;
    }
    &__left {
      width: 100%;
      margin: 0 0 2.5em 0;
    }
    &__right {
      width: 100%;
    }
  }
}

@media screen and (max-width: 576px) {
  .product-main {
    &__price {
      display: block;
    }
    .old-price {
      display: inline-block;
      margin: 0.75em 0 0 0;
    }
    &__btns {
      grid-template-columns: 100%;
      gap: 1.25em;
    }
  }
}