.catalog {
  padding: 6.125em 0 3.125em;
  overflow: hidden;
  &2 {
    padding: 6.25em 0;
  }
  &__top,
  &__bottom {
    position: relative;
  }
  &__title {
    text-align: center;
    span {
      position: relative;
      z-index: 2;
      background: #ffffff;
      padding: 0 2em;
    }
  }
  &__title2 {
    margin-bottom: 2.1875em;
    a {
      color: #B88571;
      &:hover {
        color: #676766;
      }
    }
  }
  &__menu {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin-top: 3.75em;
  }
  &__menu2 {
    margin-top: 1.25em;
  }
  &__tab {
    cursor: pointer;
    margin-bottom: 1.875em;
    &:not(:last-child) {
      margin-right: 1.825em;
    }
    &:hover {
      color: #b88571;
    }
    &--active {
      span {
        font-weight: 600;
        color: #B88571;
        text-decoration: underline;
        text-underline-offset: 0.1875em;
      }
    }
    span {
      font-size: 2.0625em;
      line-height: 120%;

    }
  }
  &__btns {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-top: 1.875em;
    &-flex {
      display: flex;
      align-items: center;
      margin-left: 1.375em;
    }
  }
  &__select {
    .nice-select {
      width: auto;
      height: auto;
      font-size: unset;
      line-height: unset;
      border: none;
      border-radius: 0;
      padding: 0;
      .current {
        font-size: 2.0625em;
        line-height: 120%;
        font-weight: 600;
      }
      .list {
        li {
          font-size: 1.5em;
          line-height: 120%;
          padding: 0.625em;
        }
      }
    }
  }
  &__btn {
    width: 2.625em;
    background: transparent;
    padding: 0;
    &:not(:first-child) {
      margin-left: 1.375em;
    }
    svg {
      width: 100%;
      height: auto;
    }
  }
  &__flex {
    display: flex;
    margin: 3.75em 0 6.25em;
  }
  &__flex2 {
    margin-bottom: 0;
  }
  &__sidebar {
    width: 23.4375em;
    margin-right: 3.125em;
  }
  &__content {
    width: calc(100% - 26.5625em);
    display: none;
    &--active {
      display: block;
    }
  }
  &__cards {
    grid-template-columns: repeat(3, calc(100% / 3 - 2.5em / 3));
    gap: 1.25em;
    display: none;
    &--active {
      display: grid;
    }
  }
  &__cards2 {
    grid-template-columns: 100%;
    gap: 2.5em;
  }
  .product-card {
    border: 0.0625em solid #C98B94;
    border-radius: 0.9375em;
    padding: 1em;
  }
  &__sidebar-title {
    span {
      font-size: 2.0625em;
      line-height: 120%;
      font-weight: 600;
    }
  }
  &__price {
    display: inline-flex;
    align-items: stretch;
    position: relative;
    margin-top: 2.125em;
    input {
      width: 6.25em;
      height: 2.5em;
      border: 0.0625em solid #B88571;
      border-radius: 0.3125em;
      font-size: 1.25em;
      line-height: 120%;
      color: #676766;
      text-align: center;
      padding: 0 0.3125em;
      &::placeholder {
        color: #888;
      }
      &:not(:first-child) {
        margin-left: 2em;
      }
    }
    span {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 1.5625em;
      height: 0.0625em;
      background: #B88571;
    }
  }
  &__checkboxes {
    display: flex;
    align-items: center;
    margin-top: 2.5em;
    .catalog__checkbox {
      &:not(:first-child) {
        margin-left: 3.125em;
      }
    }
  }
  &__checkbox {
    position: relative;
    cursor: pointer;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      margin: 0;
      opacity: 0;
      cursor: pointer;
      &:checked ~ .catalog__checkbox-text {
        &:before {
          opacity: 1;
          pointer-events: all;
        }
        &:after {
          background: #B88571;
        }
      }
    }
    &-text {
      position: relative;
      padding-left: 3.125em;
      span {
        line-height: 190%;
      }
      &:after {
        content: '';
        position: absolute;
        top: 0.1875em;
        left: 0;
        width: 2.1875em;
        height: 2.1875em;
        border: 0.0625em solid #B88571;
        border-radius: 0.3125em;
        transition: all 0.3s;
      }
      &:before {
        content: '';
        position: absolute;
        z-index: 1;
        top: 0.75em;
        left: 0.5625em;
        width: 1.125em;
        height: 1em;
        background-image: url(../img/check.svg);
        background-repeat: no-repeat;
        background-position: center;
        background-size: 1.125em 1em;
        opacity: 0;
        pointer-events: none;
        transition: all 0.3s;
      }
    }
  }
  &__items {
    margin-top: 2.5em;
  }
  &__item {
    &:not(:first-child) {
      margin-top: 2.5em;
    }
    &-show {
      display: flex;
      justify-content: space-between;
      align-items: center;
      cursor: pointer;
      span {
        font-size: 2.0625em;
        line-height: 120%;
        font-weight: 600;
      }
      i {
        flex-shrink: 0;
        width: 1.125em;
        img {
          width: 100%;
        }
      }
    }
    &-hidden {
      margin-top: 2.3125em;
      display: none;
    }
    .catalog__checkboxes {
      display: flex;
      flex-direction: column;
      align-items: start;
    }
    .catalog__checkbox {
      display: inline-block;
      &:not(:first-child) {
        margin: 2.1875em 0 0 0;
      }
    }
  }
}
.catalog-info {
  padding: 3.125em 0;
  &__inner {
    text-align: center;
  }
  &__top,
  &__bottom {
    position: relative;
  }
  &__title {
    span {
      position: relative;
      z-index: 2;
      background: #ffffff;
      padding: 0 0.9375em;
      letter-spacing: 0.0625em;
    }
  }
  &__content {
    margin: 3.125em 0;
  }
  &__text {
    margin-top: 3.125em;
    a {
      color: #B88571;
    }
  }
  &__btn {
    width: 36.25em;
    height: 3.75em;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #ffffff;
    border: 0.0625em solid #B88571;
    border-radius: 0.4375em;
    padding: 0 1.625em;
    margin: 2.5em auto 0;
    span {
      color: #B88571;
    }
    &:hover {
      background: #B88571;
      span {
        color: #ffffff;
      }
    }
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 6.25em / 3));
    gap: 3.125em;
    margin-top: 3.125em;
  }
  &__item {
    background: #F6F6F6;
    padding: 1.25em 2.375em;
    position: relative;
    &-icon {
      position: absolute;
      top: 50%;
      right: -2.5em;
      -webkit-transform: translate(0,-50%);
      -ms-transform: translate(0,-50%);
      transform: translate(0,-50%);
      width: 1.875em;
    }
    &-number {
      span {
        display: inline-block;
        font-size: 5em;
        line-height: 105%;
        font-weight: 600;
        color: #b88571;
        position: relative;
        &:after {
          content: "";
          position: absolute;
          left: 50%;
          bottom: 0.0625em;
          -webkit-transform: translate(-50%,0);
          -ms-transform: translate(-50%,0);
          transform: translate(-50%,0);
          width: 100%;
          height: 5px;
          background: #b88571;
        }
      }
    }
    &-text {
      margin-top: 1.25em;
      p a {
        color: #B88571;
        &:hover {
          color: #676766;
        }
      }
    }
  }
}
.catalog-info2 {
  padding: 3.125em 0 6.25em;
  &__inner {
    text-align: center;
  }
  &__top,
  &__bottom {
    position: relative;
  }
  &__title {
    span {
      position: relative;
      z-index: 2;
      background: #ffffff;
      padding: 0 0.9375em;
      letter-spacing: 0.0625em;
    }
  }
  &__desc {
    margin: 3.125em 0;
    p {
      a {
        color: #B88571;
      }
      &:not(:first-child) {
        margin-top: 1.375em;
      }
    }
  }
}
.product-card2 {
  display: flex;
  padding-bottom: 2.5em;
  border-bottom: 0.0625em solid #B88571;
  &__left {
    flex-shrink: 0;
    width: 13.25em;
    height: 13.25em;
    border-radius: 0.875em;
    border: 0.0625em solid #C98B94;
    text-align: center;
    padding: 1.25em;
    margin-right: 2.5em;
  }
  &__img {
    position: relative;
    &:before {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 100%;
      height: 100%;
      border: 0.09375em solid #676766;
      border-radius: 50%;
      pointer-events: none;
    }
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: calc(100% + 0.75em);
      height: calc(100% + 0.75em);
      border: 0.09375em solid #BABABA;
      border-radius: 50%;
      pointer-events: none;
    }
    img {
      width: 100%;
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__title {
    span {
      color: #B88571;
    }
  }
  &__favorite {
    width: 1.875em;
    margin-left: 2em;
    cursor: pointer;
    svg {
      width: 100%;
      height: auto;
    }
    &--active {
      svg path {
        fill: #b88571;
        stroke: #b88571;
      }
    }
  }
  &__info {
    display: flex;
    align-items: center;
    margin-top: 0.625em;
    i {
      flex-shrink: 0;
      width: 1.125em;
      margin-right: 0.625em;
      img {
        width: 100%;
      }
    }
    span {
      color: #BABABA;
    }
  }
  &__text {
    margin-top: 0.625em;
    p {
      color: #BABABA;
    }
  }
  &__characteristic {
    display: flex;
    align-items: stretch;
    margin-top: 0.625em;
    i {
      flex-shrink: 0;
      width: 0.875em;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-left: 0.625em;
      svg {
        width: 100%;
        height: auto;
      }
    }
    &:hover {
      span {
        color: #b88571;
      }
      i svg path {
        stroke: #b88571;
      }
    }
  }
  &__bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 0.625em;
  }
  &__price {
    display: flex;
    align-items: center;
    span:nth-child(1) {
      color: #b88571;
    }
    span:nth-child(2) {
      font-size: 0.8125em;
      line-height: 140%;
      color: #B88571;
      text-decoration: line-through;
      margin-left: 1.375em;
    }
  }
  &__basket {
    width: 2.5em;
    margin-left: 2em;
    svg {
      width: 100%;
      height: auto;
    }
    &:hover {
      svg path {
        stroke: #b88571;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .catalog {
    padding: 3.125em 0 1.5625em;
    &2 {
      padding: 3.125em 0;
    }
    &__title {
      span {
        padding: 0;
      }
    }
    &__tab {
      span {
        font-size: 1.6875em;
      }
    }
    &__select {
      .nice-select {
        .current {
          font-size: 1.6875em;
        }
      }
    }
    &__flex {
      display: block;
    }
    &__sidebar {
      margin: 0 0 3.125em 0;
    }
    &__content {
      width: 100%;
    }
    &__item {
      &-show {
        span {
          font-size: 1.6875em;
        }
      }
    }
    &__checkbox {
      &-text {
        &:after {
          top: 0.0625em;
        }
        &:before {
          top: 0.6875em;
        }
        span {
          font-size: 1.125em;
        }
      }
    }
  }
  .catalog-info {
    padding: 1.5625em 0;
    &__title {
      span {
        padding: 0;
      }
    }
    &__content {
      margin: 2.5em 0 0 0;
    }
    &__text {
      margin-top: 2.5em;
    }
    &__items {
      margin-top: 2.5em;
    }
  }
  .catalog-info2 {
    padding: 1.5625em 0 3.125em;
    &__desc {
      margin: 2.5em 0 0 0;
    }
  }
}

@media screen and (max-width: 768px) {
  .catalog {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 1.25em / 2));
    }
    &__cards2 {
      grid-template-columns: 100%;
    }
  }
  .catalog-info {
    &__items {
      grid-template-columns: repeat(2, calc(50% - 1.875em / 2));
      gap: 1.875em;
    }
    &__item {
      &-icon {
        display: none;
      }
      &-number {
        span {
          font-size: 3.75em;
        }
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .catalog {
    &__sidebar {
      width: 100%;
    }
    &__btns {
      flex-direction: column;
      align-items: start;
      &-flex {
        margin: 1.375em 0 0 0;
      }
    }
  }
  .catalog-info {
    &__btn {
      width: 100%;
    }
    &__items {
      grid-template-columns: 100%;
      gap: 3.125em;
    }
    &__item {
      &-icon {
        display: block;
        top: unset;
        left: 50%;
        right: 0;
        bottom: -2.125em;
        transform: translate(-50%, 0) rotate(90deg);
      }
    }
  }
  .product-card2 {
    display: block;
    &__left {
      width: 100%;
      height: auto;
      margin: 0 0 2em 0;
    }
  }
}

@media screen and (max-width: 400px) {
  .catalog {
    &__cards {
      grid-template-columns: 100%;
    }
  }
}