.article {
  padding: 6.25em 0 3.125em;
  &__top,
  &__bottom {
    position: relative;
  }
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    span {
      background: #ffffff;
      color: #2C4158;
      text-transform: uppercase;
      padding: 0 2em;
    }
  }
  &__content {
    margin: 5em 0;
  }
  &__img {
    margin-bottom: 3.125em;
    img {
      width: 100%;
    }
  }
  &__text {
    p {
      &:not(:first-child) {
        margin-top: 1.375em;
      }
      a, span {
        font-weight: 600;
        color: #B88571;
      }
    }
  }
  &__nav {
    margin: 3.125em 0;
  }
  &__list {
    background: #FFEBE4;
    border-radius: 1.875em;
    padding: 2.5em;
    margin-top: 1.25em;
    li {
      display: flex;
      &:not(:first-child) {
        margin-top: 0.3125em;
      }
      i {
        flex-shrink: 0;
        width: 2.375em;
        margin-top: 0.3125em;
        margin-right: 1em;
        img {
          width: 100%;
        }
      }
      span a {
        color: #b88571;
      }
    }
  }
  &__slider {
    margin: 3.75em 0 2.5em 0;
    &-title {
      text-align: center;
      padding: 1.75em 0;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 50%;
        transform: translate(-50%, 0);
        width: 46.875em;
        height: 0.0625em;
        background: #B88571;
      }
      &:before {
        content: '';
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, 0);
        width: 46.875em;
        height: 0.0625em;
        background: #B88571;
      }
      p {
        color: #B88571;
      }
    }
  }
  .swiper {
    padding: 0 3em;
    margin-top: 3.75em;
  }
  .swiper-slide {
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    &-active,
    &-next,
    &-next + .swiper-slide {
      opacity: 1;
      pointer-events: all;
    }
    img {
      width: 100%;
      border-radius: 0.3125em;
    }
  }
  .swiper-button-prev {
    left: 0;
  }
  .swiper-button-next {
    right: 0;
  }
  &__subtitle {
    text-align: center;
    margin-bottom: 2.1875em;
    span {
      color: #2C4158;
      text-transform: uppercase;
    }
  }
  &__nav2 {
    &-title {
      margin-top: 1.375em;
    }
  }
  &__list2 {
    background: transparent;
    border-radius: 0;
    padding: 0;
    margin-top: 1.375em;
  }
}
.current-articles {
  padding: 3.125em 0 6.25em;
  &__title {
    span {
      text-transform: uppercase;
      color: #2C4158;
    }
  }
  &__slider {
    margin-top: 3.75em;
  }
  .swiper {
    overflow: unset;
    padding: 0 3em;
  }
  .swiper-button-prev {
    left: 0;
  }
  .swiper-button-next {
    right: 0;
  }
  .swiper-slide {
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    &-active,
    &-next,
    &-next + .swiper-slide {
      opacity: 1;
      pointer-events: all;
    }
  }
}

@media screen and (max-width: 992px) {
  .article {
    padding: 3.125em 0 1.5625em;
    &__content {
      margin: 2.5em 0 0 0;
    }
    &__img {
      margin-bottom: 1.25em;
    }
    &__nav {
      margin: 2.5em 0;
    }
    &__list {
      padding: 1.875em 1.25em;
     li i {
        margin-top: 0;
      }
    }
    &__slider {
      margin-top: 2.5em;
      &-title {
        &:after {
          width: 100%;
        }
        &:before {
          width: 100%;
        }
      }
    }
    .swiper {
      margin-top: 2.5em;
    }
    &__list2 {
      padding: 0;
    }
  }
  .current-articles {
    padding: 1.5625em 0 3.125em;
    &__slider {
      margin-top: 2.5em;
    }
  }
}

@media screen and (max-width: 768px) {
  .article {
    .swiper-slide {
      &-next + .swiper-slide {
        opacity: 0;
        pointer-events: none;
      }
    }
  }
  .current-articles {
    .swiper-slide {
      &-next + .swiper-slide {
        opacity: 0;
        pointer-events: none;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .article {
    &__text {
      p {
        font-size: 1.25em;
      }
    }
    &__list {
      li {
        span {
          font-size: 1.25em;
        }
      }
    }
    .swiper-slide {
      &-next {
        opacity: 0;
        pointer-events: none;
      }
    }
  }
  .current-articles {
    .swiper-slide {
      &-next {
        opacity: 0;
        pointer-events: none;
      }
    }
  }
}