.purchase {
  padding: 6.25em 0 3.125em;
  &__inner {
    position: relative;
  }
  &__btn {
    position: absolute;
    top: 1.375em;
    left: -4em;
  }
  &__top {
    position: relative;
  }
  &__bottom {
    position: relative;
  }
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    span {
      background: #ffffff;
      padding: 0 2em;
    }
  }
  &__slider {
    padding: 0 3.5em;
    margin: 5em 0;
    position: relative;
  }
}
.purchase-card {
  height: 17.5em;
  border-radius: 50%;
  border: 0.125em solid #D7D8D8;
  text-align: center;
  padding: 2.375em 1.125em;
  position: relative;
  &:after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: calc(100% - 0.375em);
    height: calc(100% - 0.375em);
    border: 0.09375em solid #676766;
    border-radius: 50%;
    pointer-events: none;
  }
  &__icon {
    width: 5em;
    margin: 0 auto;
    img {
      width: 100%;
    }
  }
  &__title {
    margin-top: 1em;
    span {
      text-transform: uppercase;
      overflow: hidden;
      display: -webkit-box;
      -webkit-line-clamp:3;
      -webkit-box-orient: vertical;
    }
  }
}
.connection {
  padding: 3.125em 0;
  &__top {
    position: relative;
  }
  &__bottom {
    position: relative;
    margin-top: 5em;
  }
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    span {
      background: #ffffff;
      padding: 0 2em;
    }
  }
  &__subtitle {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-top: 2.625em;
  }
  &__flex {
    display: flex;
    align-items: stretch;
    margin: 5em 0 0 0;
  }
  &__form {
    width: 32.625em;
    margin-right: 3.625em;
  }
  &__info {
    width: calc(100% - 36.25em);
    background: #D7D8D8;
    border-radius: 1.875em;
    padding: 4.375em;
    position: relative;
    overflow: hidden;
  }
  &__img {
    position: absolute;
    left: -7.8125em;
    bottom: 0;
    width: 74.625em;
    pointer-events: none;
  }
  &__desc {
    display: grid;
    grid-template-columns: 100%;
    gap: 1.375em;
    position: relative;
    z-index: 2;
    p {
      font-weight: 700;
    }
  }
  &__form {
    &-group {
      &:not(:first-child) {
        margin-top: 0.9375em;
      }
    }
    &-btn {
      margin-top: 1.875em;
    }
    &-check {
      margin-top: 1.875em;
    }
  }
}
.stamps {
  padding: 3.125em 0;
  &__top,
  &__bottom {
    position: relative;
  }
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    span {
      background: #ffffff;
      padding: 0 2em;
    }
  }
  &__desc {
    max-width: 71.875em;
    margin: 5em auto;
    text-align: center;
    p {
      text-transform: uppercase;
    }
  }
}
.product {
  padding: 3.125em 0;
  &2 {
    padding-bottom: 6.25em;
  }
  &__top,
  &__bottom {
    position: relative;
  }
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    span {
      background: #ffffff;
      padding: 0 2em;
    }
  }
  &__slider {
    padding: 0 2em;
    margin: 2.5em 0;
    position: relative;
  }
  &__swiper {
    overflow: unset;
  }
  .swiper-button-prev {
    left: 0;
  }
  .swiper-button-next {
    right: 0;
  }
  .swiper-slide {
    opacity: 0;
    pointer-events: none;
    transition: all 0.5s;
    &-active,
    &-next,
    &-next + .swiper-slide,
    &-next + .swiper-slide + .swiper-slide {
      opacity: 1;
      pointer-events: all;
    }
  }
  &-card {
    text-align: center;
    position: relative;
    &:hover {
      transform: translate(-0.625em, -0.625em);
    }
    &__new {
      position: absolute;
      z-index: 2;
      top: 1em;
      left: 1em;
      height: 2.75em;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      background: #FFF736;
      border-radius: 0.3125em;
      padding: 0 0.9375em;
    }
    &__stock {
      position: absolute;
      z-index: 2;
      top: 1em;
      left: 1em;
      height: 2.75em;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      background: #FE4219;
      color: #ffffff;
      border-radius: 0.5625em;
      padding: 0 0.9375em;
    }
    &__discount {
      position: absolute;
      z-index: 2;
      top: 1em;
      left: 1em;
      height: 2.75em;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      background: #C98B94;
      color: #ffffff;
      border-radius: 0.5625em;
      padding: 0 0.9375em;
    }
    &__img {
      width: auto;
      height: auto;
      border-radius: 50%;
      border: 0.125em solid #D7D8D8;
      text-align: center;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: calc(100% - 0.375em);
        height: calc(100% - 0.375em);
        border: 0.09375em solid #676766;
        border-radius: 50%;
        pointer-events: none;
      }
      img {
        width: 100%;
        max-width: 100%;
        max-height: 100%;
      }
    }
    &__desc {
      margin-top: 1.5625em;
    }
    &__material {
      margin-top: 0.3125em;
      span {
        color: #D7D8D8;
      }
    }
    &__price {
      display: flex;
      justify-content: center;
      align-items: center;
      color: #B88571;
      padding: 0.1875em 0;
      border-top: 0.0625em solid #B88571;
      border-bottom: 0.0625em solid #B88571;
      span:nth-child(1) {
       font-weight: 600;
     }
      span:nth-child(2) {
        font-size: 0.9375em;
        line-height: 140%;
        text-decoration: line-through;
        margin-left: 1em;
      }
    }
    &__info {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-top: 1em;
      i {
        flex-shrink: 0;
        width: 0.75em;
        margin-right: 0.5625em;
        img {
          width: 100%;
        }
      }
      span {
        color: rgba(186, 186, 186, 0.83);
      }
    }
  }
}
.about {
  padding: 3.125em 0;
  &__top,
  &__bottom {
    position: relative;
  }
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    span {
      background: #ffffff;
      padding: 0 2em;
    }
  }
  &__desc {
    display: grid;
    grid-template-columns: 100%;
    gap: 2.5em;
    max-width: 78.125em;
    margin: 5em auto;
    text-align: center;
  }
}
.contacts {
  padding: 3.125em 0 6.25em;
  &__top,
  &__bottom {
    position: relative;
  }
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    span {
      background: #ffffff;
      padding: 0 2em;
    }
  }
  &__content {
    display: flex;
    align-items: center;
    margin: 5em 0;
    padding: 0 4.25em;
  }
  &__desc {
    width: 22.5em;
    margin-right: 3.125em;
  }
  .map {
    width: calc(100% - 25.625em);
    height: 25.3125em;
    .ymaps-2-1-79-map {
      width: 100% !important;
      height: 100% !important;
    }
  }
  &__tel {
    margin-top: 2.5em;
    p a {
      &:hover {
        color: #C98B94;
      }
    }
  }
}

@media screen and (max-width: 1600px) {
  .purchase {
    &__btn {
      position: static;
      margin-top: 2em;
    }
  }
}

@media screen and (max-width: 1400px) {
  .purchase-card {
    height: 18.5625em;
  }
}

@media screen and (max-width: 992px) {
  .purchase {
    padding: 3.125em 0 1.5625em;
    &__title {
      span {
        padding: 0;
      }
    }
    &__slider {
      margin: 2.5em 0;
    }
  }
  .purchase-card {
    height: 16.4375em;
  }
  .connection {
    padding: 1.5625em 0;
    &__top {
      margin-top: 0;
    }
    &__title {
      span {
        padding: 0;
      }
    }
    &__flex {
      margin-top: 2.5em;
    }
    &__form {
      width: 25em;
      margin-right: 1.5625em;
    }
    &__info {
      width: calc(100% - 26.5625em);
      padding: 1.875em 1.25em;
    }
    &__img {
      left: -2em;
      width: 60em;
    }
    &__bottom {
      display: none;
    }
  }
  .stamps {
    padding: 1.5625em 0;
    &__title {
      span {
        padding: 0;
      }
    }
    &__desc {
      margin: 2.5em 0 0 0;
    }
  }
  .product {
    padding: 1.5625em 0;
    &2 {
      padding-bottom: 3.125em;
    }
    &__title {
      span {
        padding: 0;
      }
    }
    &__slider {
      margin: 2.5em 0 0 0;
    }
    .swiper-slide {
      &-next + .swiper-slide + .swiper-slide {
        opacity: 0;
        pointer-events: none;
      }
    }
  }
  .about {
    padding: 1.5625em 0;
    &__title {
      span {
        padding: 0;
      }
    }
    &__desc {
      gap: 1.25em;
      margin: 2.5em 0 0 0;
    }
  }
  .contacts {
    padding: 1.5625em 0 3.125em;
    &__title {
      span {
        padding: 0;
      }
    }
    &__content {
      margin: 2.5em 0 0;
      padding: 0;
    }
    &__desc {
      width: 18.75em;
      margin-right: 1.875em;
    }
    .map {
      width: calc(100% - 20.625em);
    }
  }
}

@media screen and (max-width: 768px) {
  .purchase-card {
    height: 18.4375em;
  }
  .connection {
    &__flex {
      flex-direction: column;
    }
    &__form {
      width: 100%;
      margin: 2em 0 0 0;
    }
    &__info {
      order: -1;
      width: 100%;
    }
  }
  .product {
    .swiper-slide {
      &-next + .swiper-slide {
        opacity: 0;
        pointer-events: none;
      }
    }
  }
  .contacts {
    &__content {
      flex-direction: column;
      align-items: stretch;
    }
    &__desc {
      width: 100%;
      margin: 0 0 2em;
    }
    .map {
      width: 100%;
    }
    &__tel {
      margin-top: 1.25em;
    }
  }
}

@media screen and (max-width: 576px) {
  .purchase-card {
    height: 26.5em;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .connection {
    &__desc {
      p {
        font-size: 1.125em;
      }
    }
  }
  .product {
    .swiper-slide {
      &-next {
        opacity: 0;
        pointer-events: none;
      }
    }
  }
}

@media screen and (max-width: 400px) {
  .purchase-card {
    height: 15.5em;
  }
}