.footer {
  background: #F6F4F5;
  position: relative;
  &__logo {
    position: absolute;
    top: 1.75em;
    left: 50%;
    transform: translate(-50%, 0);
    display: block;
    width: 21.6875em;
    img {
      width: 100%;
    }
  }
}
.footer-top {
  padding: 7.8125em 0 1.5625em;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__right {
    margin-left: 1.25em;
  }
  &__tel {
    display: block;
  }
  &__location {
    display: block;
    margin-top: 0.9375em;
  }
  &__socials {
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
  &__social {
    width: 2.625em;
    height: 2.625em;
    display: flex;
    justify-content: center;
    align-items: center;
    background: transparent;
    border: 0.0625em solid #676766;
    border-radius: 50%;
    &:not(:first-child) {
      margin-left: 0.625em;
    }
    svg {
      width: 1.5em;
      height: auto;
    }
    &:hover {
      background: #C98B94;
      border: 0.0625em solid #C98B94;
      svg path {
        stroke: #ffffff;
      }
    }
  }
}
.footer-bottom {
  padding: 1.25em 0;
  border-top: 0.125em solid #E9E9E9;
  &__info {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__link {
    &:not(:first-child) {
      margin-left: 2em;
    }
    &:hover {
      color: #C98B94;
    }
  }
}

@media screen and (max-width: 992px) {
  .footer {
    &__logo {
      width: 12.5em;
    }
  }
  .footer-top {
    padding: 9.375em 0 1.5625em;
  }
}

@media screen and (max-width: 768px) {
  .footer-bottom {
    &__info {
     flex-direction: column;
      align-items: start;
    }
    &__link {
      &:not(:first-child) {
        margin: 1.25em 0 0 0;
      }
    }
  }
}
