.news {
  padding: 6.25em 0;
  &__top,
  &__bottom {
    position: relative;
  }
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    span {
      background: #ffffff;
      color: #2C4158;
      text-transform: uppercase;
      padding: 0 2em;
    }
  }
  &__content {
    margin: 5em 0;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 6.25em / 3));
    gap: 3.125em;
  }
}
.new-card {
  background: #FFF;
  border: 0.125em solid transparent;
  box-shadow: 0 0 1.125em 0 rgba(217, 174, 157, 0.27);
  border-radius: 0.3125em;
  padding: 1.25em 0.875em;
  &:hover {
    border: 0.125em solid #b88571;
  }
  &__img {
    position: relative;
    padding-bottom: 72%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      border-radius: 0.3125em;
    }
  }
  &__desc {
    margin-top: 1.4375em;
  }
  &__title {
    span {
      font-weight: 600;
      text-transform: uppercase;
    }
  }
  &__text {
    margin-top: 0.9375em;
  }
  &__link {
    background: transparent;
    padding: 0;
    margin-top: 0.9375em;
    span {
      text-decoration: underline;
      text-underline-offset: 0.1875em;
      transition: all 0.5s;
      &:hover {
        color: #b88571;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .news {
    padding: 3.125em 0;
    &__title {
      span {
        padding: 0;
      }
    }
    &__content {
      margin: 2.5em 0 0 0;
    }
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 3.75em / 3));
      gap: 1.875em;
    }
  }
}

@media screen and (max-width: 768px) {
  .news {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 1.875em / 2));
    }
  }
}

@media screen and (max-width: 400px) {
  .news {
    &__cards {
      grid-template-columns: 100%;
    }
  }
}